*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

a,
button,
input,
input::placeholder,
textarea,
textarea::placeholder {
  font-family: "RalewayDev", "MontserratDev", "sans serif";
}

body {
  margin: 0;
  min-width: 320px;
  overflow-x: hidden;
  font-family: "RalewayDev", "MontserratDev", "sans serif";
  color: #2C2C2C;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

a:hover,
button:hover,
input:hover {
  cursor: pointer;
}

.container {
  width: 100%;
  max-width: calc(1320px + 40px);
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
}

.title-1 {
  font-weight: bold;
  font-size: 48px;
  line-height: 110%;
  text-transform: uppercase;
  color: #2c2c2c;
}

.title-1_white {
  color: #ffffff;
}

.btn-link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  white-space: nowrap;
  padding: 0 95px;
  min-height: 55px;
  border: 1px solid #ffffff;
  font-size: 15px;
  letter-spacing: 0.15em;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #ffffff;
}

.btn-link_black {
  border-color: #000;
  color: #000;
}

.text {
  font-size: 15px;
  line-height: 150%;
  color: #2C2C2C;
}

.text_type-1 {
  font-weight: bold;
  font-size: 13px;
  line-height: 150%;
}

.text_type-2 {
  font-size: 12px;
  line-height: 150%;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #a0a0a0;
}

.text_type-3 {
  font-size: 15px;
  line-height: 150%;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #5C5C5C;
}

.ttu {
  text-transform: uppercase;
}

.ui-input {
  border: 0;
  outline: 0;
  font-size: 13px;
  line-height: 19px;
  height: 40px;
}

.ui-input::placeholder {
  font-size: 13px;
  line-height: 19px;
}

.ui-input_icon {
  padding-right: 40px;
}

.ui-input2 {
  position: relative;
  height: 35px;
  padding-bottom: 10px;
  border-bottom: 1px solid #2c2c2c;
}

.ui-input2 input {
  border: 0;
  outline: 0;
  font-size: 13px;
  line-height: 19px;
  width: 100%;
}

.ui-input2 input::placeholder {
  font-size: 13px;
  line-height: 19px;
}

.ui-input2 img {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(calc(-50% - 5px));
}

.ui-card__container {
  display: flex;
}

.ui-card__img {
  max-height: 133px;
  width: auto;
  display: block;
}

.ui-card__content {
  display: flex;
  flex-direction: column;
  margin-left: 97px;
}

.ui-card__name {
  margin-bottom: 5px;
  font-weight: bold;
  font-size: 13px;
  line-height: 150%;
}

.ui-card__code {
  margin-bottom: 22px;
  font-size: 12px;
  line-height: 150%;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #a0a0a0;
}

.ui-card__sizes-and-colors {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}

.ui-card__sizes {
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.ui-card__size {
  font-size: 15px;
  line-height: 150%;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #5c5c5c;
}

.ui-card__colors {
  display: flex;
  align-items: center;
}

.ui-card__color {
  position: relative;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 1px solid #A0A0A0;
  background-color: var(--bgColor);
}

.ui-card__price {
  margin-top: auto;
  font-weight: 600;
  font-size: 13px;
  line-height: 150%;
  letter-spacing: 0.15em;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #000000;
}

.ui-card2__img {
  width: 100%;
  display: block;
  margin-bottom: 20px;
}

.ui-card2__name-and-price {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.ui-card2__name {
  font-size: 13px;
  line-height: 150%;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #2c2c2c;
}

.ui-card2__price {
  font-size: 13px;
  line-height: 150%;
  text-align: right;
  letter-spacing: 0.15em;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #2c2c2c;
}

.ui-card2__sizes-and-colors {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.ui-card2__sizes {
  display: flex;
  align-items: center;
}

.ui-card2__size {
  font-size: 12px;
  line-height: 150%;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #a0a0a0;
}

.ui-card2__size:not(:last-child) {
  margin-right: 10px;
}

.ui-card2__size.active {
  color: #2c2c2c;
}

.ui-card2__colors {
  display: flex;
  align-items: center;
}

.ui-card2__color {
  position: relative;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 1px solid #A0A0A0;
  background-color: var(--bgColor);
}

.ui-card2__color:not(:last-child) {
  margin-right: 10px;
}

.ui-card3 {
  position: relative;
}

.ui-card3__wrapper {
  position: relative;
}

.ui-card3__img {
  display: block;
}

.ui-card3__link {
  position: absolute;
  height: 89px;
  padding: 35px 20px 10px 20px;
  display: flex;
  justify-content: space-between;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #000000;
  background-color: #fff;
  opacity: 0.8;
  bottom: 0;
  left: 0;
  width: 100%;
}

.ui-card3__link-text {
  display: inline-block;
  max-width: 175px;
  text-decoration: none;
  font-size: 13px;
  line-height: 150%;
}

.ui-card3__link-icon {
  align-self: flex-start;
  margin-top: 5px;
}

.top-message {
  background-color: #2C2C2C;
}

.top-message__container {
  height: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.top-message__text {
  font-weight: 300;
  font-size: 13px;
  color: #fff;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 65px;
}

.header__left {
  display: flex;
  align-items: center;
}

.header__menu {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 22px;
  min-width: 22px;
  flex: 0 0 22px;
  margin-right: 15px;
  border: 0;
  outline: 0;
  background-color: transparent;
}

.header__menu.active .header__menu-icon-bar {
  display: none;
}

.header__menu.active .header__menu-icon-bar-mobile {
  display: none;
}

.header__menu.active .header__menu-icon-close {
  display: block;
}

.header__menu-icon-bar {
  pointer-events: none;
}

.header__menu-icon-bar-mobile {
  pointer-events: none;
  display: none;
}

.header__menu-icon-close {
  display: none;
  pointer-events: none;
}

.header__items {
  display: flex;
  align-items: center;
}

.header__items_left {
  margin-right: 40px;
}

.header__items_right {
  margin-right: 40px;
}

.header__item {
  text-decoration: none;
}

.header__item:not(:last-child) {
  margin-right: 40px;
}

.header__item_phone {
  font-feature-settings: "pnum" on, "lnum" on;
}

.header__item_cart {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.header__item_search {
  display: none;
}

.header__search {
  position: relative;
  padding-right: 20px;
  border-bottom: 1px solid transparent;
  transition: all 0.3s ease;
  cursor: pointer;
}

.header__search.active {
  padding-right: 40px;
  border-bottom-color: #2C2C2C;
}

.header__search.active .header__input {
  width: 200px;
  max-width: none;
  flex: 0 0 200px;
}

.header__input {
  width: 0;
  max-width: 0;
  flex: 0 0 0;
  transition: all 0.3s ease;
}

.header__input-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
}

.header__center {
  transition: all 0.3s ease;
}

.header__center.disabled {
  max-width: 0;
  opacity: 0;
  z-index: -1;
  visibility: hidden;
}

.header__logo-img {
  display: block;
}

.header__right {
  display: flex;
  align-items: center;
}

.header__navigation {
  position: absolute;
  top: calc(65px + 28px);
  height: calc(100vh - (65px + 28px));
  left: 0;
  right: 0;
  width: 100%;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  transition: all 0.3s ease;
}

.header__navigation.active {
  opacity: 1;
  visibility: visible;
  z-index: 100;
}

.header__navigation-all-items {
  display: flex;
  justify-content: space-between;
  padding: 30px 30px 80px 0;
  width: 50%;
}

.header__navigation-items:not(:last-child) {
  margin-right: 45px;
}

.header__navigation-items_mobile {
  display: none;
  margin-bottom: 20px;
}

.header__navigation-item {
  margin-bottom: 10px;
}

.header__navigation-item:last-child {
  margin-bottom: 0;
}

.header__navigation-item_title {
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 14px;
  line-height: 15px;
  color: #2c2c2c;
}

.header__navigation-item_title.active .header__navigation-item-icon {
  transform: rotate(180deg);
}

.header__navigation-item_title.active + .header__navigation-items-menu {
  max-height: 100%;
