*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

a,
button,
input,
input::placeholder,
textarea,
textarea::placeholder {
  font-family: "RalewayDev", "MontserratDev", "sans serif";
}

body {
  margin: 0;
  min-width: 320px;
  overflow-x: hidden;
  font-family: "RalewayDev", "MontserratDev", "sans serif";
  color: $accent;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

a,
button,
input {
  &:hover {
    cursor: pointer;
  }
}

.container {
  width: 100%;
  max-width: calc(1320px + 40px);
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  @include media("<=991px") {
    padding-left: 20px;
    padding-right: 20px;
  }
  @include media("<=576px") {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.title-1 {
  // TODO: font-family: 'MontserratDev';
  font-weight: bold;
  font-size: 48px;
  line-height: 110%;
  text-transform: uppercase;
  color: #2c2c2c;
  &_white {
    color: #ffffff;
  }
  @include media("<=991px") {
    font-size: 24px;
    line-height: 110%;
    text-transform: uppercase;
  }
  @include media("<=768px") {
  }
  @include media("<=480px") {
  }
}

.btn-link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  white-space: nowrap;
  padding: 0 95px;
  min-height: 55px;

  border: 1px solid #ffffff;
  font-size: 15px;
  letter-spacing: 0.15em;
  font-feature-settings: "pnum" on, "lnum" on;
  color: #ffffff;
  @include media("<=991px") {
  }
  @include media("<=768px") {
  }
  @include media("<=480px") {
  }
  &_black {
    border-color: #000;
    color: #000;
  }
}

.text {
  font-size: 15px;
  line-height: 150%;
  color: $accent;
  @include media("<=991px") {
    font-size: 13px;
    line-height: 19px;
  }

  &_type-1 {
    font-weight: bold;
    font-size: 13px;
    line-height: 150%;
  }
  &_type-2 {
    font-size: 12px;
    line-height: 150%;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #a0a0a0;
  }
  &_type-3 {
    font-size: 15px;
    line-height: 150%;
    font-feature-settings: "pnum" on, "lnum" on;
    color: $grey;
  }
}

.ttu {
  text-transform: uppercase;
}

.ui-input {
  border: 0;
  outline: 0;
  font-size: 13px;
  line-height: 19px;
  height: 40px;
  &::placeholder {
    font-size: 13px;
    line-height: 19px;
  }
  &_icon {
    padding-right: 40px;
  }
}

.ui-input2 {
  position: relative;
  height: 35px;
  padding-bottom: 10px;
  border-bottom: 1px solid #2c2c2c;
  @include media("<=991px") {
  }
  input {
    border: 0;
    outline: 0;
    font-size: 13px;
    line-height: 19px;
    width: 100%;
    @include media("<=991px") {
      font-size: 15px;
      font-feature-settings: "pnum" on, "lnum" on;
      color: #a0a0a0;
    }
    &::placeholder {
      font-size: 13px;
      line-height: 19px;
      @include media("<=991px") {
        font-size: 15px;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #a0a0a0;
      }
    }
  }
  img {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(calc(-50% - 5px));
    @include media("<=991px") {
    }
  }
}

